    monospace;
}

nav {
    display: flex;
    max-width: 100%;
    max-height: 10vh;
    align-items: center;
    padding: 16px 32px;
}

.NavBar_logo__3hXp8 {
    width: 20px;
}

.NavBar_logo__3hXp8 img { 
    height: 5vh;
}

img {
    height: 5vh;
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

nav ul li {
    display: inline;
}

nav ul li a {
    padding: 8px 8px;
}

nav ul li a:nth-child(0) {
    padding: 8px 8px;
}

