.App {
  text-align: center;
  font-family: 'Righteous';
  background-color: #F7F2EA;
  min-height: 100vh;
}

.landing-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  min-height: 80vh;
  max-height: 90vh;
}

.title-container {
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  padding: 0 16px;
}

.landing-page-title {
  font-size: 3rem;
  width: 100%;
}

.landing-page-subtitle {
  font-size: 1.5rem;
  width: 100%;
}

.landing-page-image-container {
  position: relative;
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  width: 100%;
  min-height: 40vh;
}

.landing-image {
  position: absolute;
  right: 0;
  bottom: 0;
  max-width: 100%;
  height: auto;
}

.visit-app-button {
  color: white;
  background-color: #CEAF7F;
  border: none;
  font-family: 'Righteous';
  padding: 20px;
  border-radius: 20px;;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {

}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {

}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .landing-page-image-container {
    position: relative;
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    width: 100%;
    min-height: 60vh;
  }

  .landing-image {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 100%;
    max-height: auto;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .landing-container {
    display: grid;
    grid-template-columns: 40% 60%;
    min-height: 80vh;
    max-height: 90vh;
  }

  .title-container {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    align-self: center;
    padding: 0 0 0 25%;
    text-align: left;
  }

  .landing-page-title {
    font-size: 3rem;
  }

  .landing-page-image-container {
    position: relative;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }

  .landing-image {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 90%;
    height: auto;
  }
}

/* Portrait and Landscape */
@media only screen 
  and (min-width: 1024px) 
  and (max-height: 1366px) 
  and (-webkit-min-device-pixel-ratio: 1.5) {
    .landing-container {
      display: grid;
      grid-template-columns: 40% 60%;
      min-height: 30vh;
      max-height: 80vh;
    }

    .title-container {
      grid-column: 1 / 2;
      grid-row: 1 / 2;
      align-self: center;
      padding: 0 0 0 20%;
      text-align: left;
    }
  
    .landing-page-title {
      font-size: 3rem;
    }
  
    .landing-page-image-container {
      position: relative;
      grid-column: 2 / 3;
      grid-row: 1 / 2;
    }
  
    .landing-image {
      position: absolute;
      right: 0;
      bottom: 0;
      max-width: 90%;
      height: auto;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {

  .landing-container {
    display: grid;
    grid-template-columns: 40% 60%;
    min-height: 80vh;
    max-height: 90vh;
  }

  .title-container {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    align-self: center;
    padding: 0 0 0 20%;
    text-align: left;
  }

  .landing-page-title {
    font-size: 3rem;
  }

  .landing-page-image-container {
    position: relative;
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }

  .landing-image {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 90%;
    height: auto;
  }
}

